<template>
  <div class="container">
    <MainHeader :searchState="isSearchState" />
    <div class="title">
      <i @click="$router.back()" class="fa-solid fa-arrow-left px-3"></i>
      <span>搜尋結果</span>
    </div>
    <div v-if="isLoading">...Loading</div>
    <div v-else class="search-list mt-3">
      <div
        v-for="spot in searchResult"
        :key="spot.id"
        class="search-card card mb-2"
      >
        <div class="card-image">
          <router-link
            :to="{
              name: `individual-${category.enTitle}`,
              params: { area: $route.params.area, id: spot.id },
            }"
          >
            <img
              :src="filterImage(spot.picture.PictureUrl1)"
              class="card-img-top"
              :alt="spot.name"
            />
          </router-link>
        </div>
        <div class="spot-body card-body">
          <router-link
            :to="{
              name: `individual-${category.enTitle}`,
              params: { area: $route.params.area, id: spot.id },
            }"
          >
            <h3 class="card-title">{{ spot.name }}</h3>
            <p class="card-text">{{ spot.city }}</p>
          </router-link>
        </div>
      </div>
    </div>
    <div class="btn-section">
      <button
        @click="showMore"
        class="moreBtn btn btn-primary"
        :disabled="isLoading"
      >
        <div
          v-if="isProcessing"
          class="spinner-border spinner-border-sm text-light"
          role="status"
        >
          <span class="visually-hidden">Loading...</span>
        </div>
        <span v-else>更多景點</span>
      </button>
    </div>
  </div>
</template>

<script>
import MainHeader from "./../components/MainHead.vue";
import spotAPI from "./../apis/scenicSpot";
import foodAPI from "./../apis/tastyFood";
import hotelAPI from "./../apis/hotelStay";
import activityAPI from "./../apis/activityFun";
import { useStore } from "vuex";
import { ref, computed, onBeforeMount } from "vue";
export default {
  components: {
    MainHeader,
  },
  setup() {
    const top = ref(12);
    const skip = ref(0);
    const keyword = ref("");
    const searchResult = ref([]);
    const resultList = ref(null);
    const search = ref(null);
    const isLoading = ref(false);
    const isProcessing = ref(false);
    const isSearchState = ref(true);
    // vuex
    const store = useStore();
    const currentArea = computed(() => store.state.currentArea);
    const category = computed(() => store.state.category);
    const searchKeyWord = computed(() => store.state.searchKeyWord);
    // function
    onBeforeMount(() => {
      keyword.value = searchKeyWord.value;
      fetchSearchList(keyword.value);
    });
    const searchSpot = async (keyword) => {
      try {
        isLoading.value = true;
        let tempArr = [];
        const { data } = await spotAPI.getAllSpot({
          filter: category.value
            ? `${encodeURIComponent("$")}filter=${encodeURIComponent(
                `contains(ScenicSpotName, '${keyword}')`
              )}&`
            : "",
          top: `${encodeURIComponent("$")}top=${top.value}&`,
          skip: `${encodeURIComponent("$")}skip=${skip.value}&`,
        });
        tempArr = data.map((result) => ({
          id: result.ScenicSpotID,
          name: result.ScenicSpotName,
          city: result.Address,
          picture: result.Picture ? result.Picture : {},
        }));
        searchResult.value.push(...tempArr);
        isLoading.value = false;
      } catch (error) {
        isLoading.value = false;
        console.log(error);
      }
    };
    const searchFood = async (keyword) => {
      try {
        isLoading.value = true;
        let tempArr = [];
        const { data } = await foodAPI.getAllRestaurant({
          filter: category.value
            ? `${encodeURIComponent("$")}filter=${encodeURIComponent(
                `contains(RestaurantName, '${keyword}')`
              )}&`
            : "",
          top: `${encodeURIComponent("$")}top=${top.value}&`,
          skip: `${encodeURIComponent("$")}skip=${skip.value}&`,
        });
        tempArr = data.map((result) => ({
          id: result.RestaurantID,
          name: result.RestaurantName,
          city: result.Address,
          picture: result.Picture ? result.Picture : {},
        }));
        searchResult.value.push(...tempArr);
        isLoading.value = false;
      } catch (error) {
        isLoading.value = false;
        console.log(error);
      }
    };
    const searchHostel = async (keyword) => {
      try {
        isLoading.value = true;
        let tempArr = [];
        const { data } = await hotelAPI.getAllHotel({
          filter: category.value
            ? `${encodeURIComponent("$")}filter=${encodeURIComponent(
                `contains(HotelName, '${keyword}')`
              )}&`
            : "",
          top: `${encodeURIComponent("$")}top=${top.value}&`,
          skip: `${encodeURIComponent("$")}skip=${skip.value}&`,
        });
        tempArr = data.map((result) => ({
          id: result.HotelID,
          name: result.HotelName,
          city: result.Address,
          picture: result.Picture ? result.Picture : {},
        }));
        searchResult.value.push(...tempArr);
        isLoading.value = false;
      } catch (error) {
        isLoading.value = false;
        console.log(error);
      }
    };
    const searchActivity = async (keyword) => {
      try {
        isLoading.value = true;
        let tempArr = [];
        const { data } = await activityAPI.getAllActivity({
          filter: category.value
            ? `${encodeURIComponent("$")}filter=${encodeURIComponent(
                `contains(ActivityName, '${keyword}')`
              )}&`
            : "",
          top: `${encodeURIComponent("$")}top=${top.value}&`,
          skip: `${encodeURIComponent("$")}skip=${skip.value}&`,
        });
        tempArr = data.map((result) => ({
          id: result.ActivityID,
          name: result.ActivityName,
          city: result.Address,
          picture: result.Picture ? result.Picture : {},
        }));
        searchResult.value.push(...tempArr);
        isLoading.value = false;
      } catch (error) {
        isLoading.value = false;
        console.log(error);
      }
    };
    const fetchSearchList = async (keyword) => {
      if (keyword.trim().length !== 0) {
        switch (category.value.enTitle) {
          case "spot":
            searchSpot(keyword);
            break;
          case "food":
            searchFood(keyword);
            break;
          case "hostel":
            searchHostel(keyword);
            break;
          case "activity":
            searchActivity(keyword);
            break;
          default:
            break;
        }
      } else {
        searchResult.value = [];
      }
    };
    const filterImage = computed(() => {
      return function (image) {
        return (
          image ||
          "https://i.postimg.cc/fyZnBVXy/pexels-tsang-chung-yee-669963.jpg"
        );
      };
    });
    const showMore = () => {
      isProcessing.value = true;
      skip.value += 12;
      setTimeout(() => {
        fetchSearchList(keyword.value);
        isProcessing.value = false;
      }, 1000);
    };
    return {
      keyword,
      searchResult,
      top,
      skip,
      isLoading,
      resultList,
      search,
      isProcessing,
      isSearchState,
      currentArea,
      category,
      searchKeyWord,
      filterImage,
      showMore,
    };
  },
  // watch: {
  //   searchKeyWord(keyword) {
  //     this.fetchSearchList(keyword);
  //   },
  // },
};
</script>

<style lang="scss" scoped>
.title {
  margin-top: 155px !important;
  font-size: 1.5rem;
  i {
    cursor: pointer;
  }
  span {
    font-weight: 700;
  }
}
.search-card {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: auto;
  overflow: hidden;
  padding: 0.5rem;
}
.card-image {
  align-self: center;
  width: 30vw;
  height: 30vw;
  overflow: hidden;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.5s ease-in-out;
  }
  img:hover {
    transform: scale(1.2);
  }
}
.badge-item {
  font-size: 0.5rem;
}
.btn-section {
  width: 100%;
  text-align: center;
  padding: 1rem 0;
}
@media screen and (min-width: 480px) {
  .title {
    margin-top: 215px !important;
  }
}
// 平板
@media screen and (min-width: 768px) {
  .search-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-auto-rows: auto;
    gap: 0.75rem;
  }
  .search-card {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
  }
  .card-image {
    width: 100%;
    height: 20vh;
  }
}
// PC
@media screen and (min-width: 960px) {
  .title {
    margin-top: 155px !important;
  }
}
</style>
